.container > div {
  display: -webkit-flex;
  display: flex;
  -webkit-justify-content: center;
  justify-content: center;
  -webkit-align-items: center;
  align-items: center;
  font-size: 2em;
  color: #ffeead;
}

.container > div > img {
  width: 100%;
  height: 100%;
  object-fit: contain;
}

.container {
  display: grid;
  grid-gap: 5px;
  grid-template-columns: repeat(auto-fit, minmax(100px, 1fr));
  grid-auto-rows: 75px;
  grid-auto-flow: dense;
}

.horizontal {
  grid-column: span 2;
}

.vertical {
  grid-row: span 2;
}

.big {
  grid-column: span 2;
  grid-row: span 2;
}

.container img:hover {
  -webkit-transform: scale(1.3);
  transform: scale(1.3);
  transition: all 0.3s ease-in-out;
  -webkit-filter: invert(0%);
  filter: invert(0%);
}

.container img {
  -webkit-filter: invert(50%);
  filter: invert(50%);
}

